@charset "UTF-8";
/* 版心样式 */
.con {
  width: 1239px;
  margin: 0 auto; }

.ruler {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(243, 242, 242, 0.1);
  border: 1px solid #eeeeee;
  z-index: 1; }
  .ruler .rulerBox {
    background-color: white;
    width: 1025px;
    height: 665px;
    -webkit-box-shadow: 0 0 5px 3px #e1e1e1;
    box-shadow: 0 0 5px 3px #e1e1e1;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    padding: 0 45px; }
    .ruler .rulerBox h2 {
      text-align: center;
      font-size: 22px;
      color: black;
      margin-top: 34px; }
    .ruler .rulerBox h3 {
      margin: 22px 0 16px 0; }
    .ruler .rulerBox h3, .ruler .rulerBox h4 {
      text-align: center;
      font-size: 14px;
      line-height: 14px;
      color: #585858; }
      .ruler .rulerBox h3 b, .ruler .rulerBox h4 b {
        font-weight: bold; }
    .ruler .rulerBox p.readTitle {
      font-size: 18px;
      color: #2875cc;
      margin: 24px 0 10px 0; }
    .ruler .rulerBox p.read {
      width: 1023px;
      height: 94px;
      border: 1px solid #e6e6e6;
      overflow-x: auto;
      white-space: pre-wrap; }
    .ruler .rulerBox .buttom {
      text-align: center;
      margin-top: 25px; }
      .ruler .rulerBox .buttom a {
        padding: 8px 24px;
        border: 1px solid #cacccf;
        border-radius: 5px;
        color: #555555; }
        .ruler .rulerBox .buttom a:first-of-type {
          margin-right: 25px; }
        .ruler .rulerBox .buttom a:last-of-type {
          border-color: #ff4c4c;
          color: #ff4c4c; }
        .ruler .rulerBox .buttom a:hover {
          border-color: #ff4c4c;
          color: #ff4c4c;
          background-color: #fff6f6; }

header {
  height: 110px;
  border-bottom: 1px solid #dd00a7;
  padding-top: 40px; }
  header .headerCon {
    height: 110px; }
    header .headerCon h1 {
      width: 355px;
      height: 69px;
      float: left; }
    header .headerCon p {
      float: right;
      margin: 26px 4px 0 0;
      font-size: 13px;
      color: #333333; }
      header .headerCon p a {
        font-size: 13px;
        color: #2875cc;
        padding-left: 4px; }

main {
  min-height: 610px;
  padding-top: 60px;
  position: relative; }
  main form {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    width: 377px;
    min-height: 560px;
    padding-top: 20px; }
    main form > div span {
      display: block;
      height: 25px;
      line-height: 25px;
      color: red; }
    main form > div input {
      width: 183px;
      height: 52px;
      border: 1px solid #cccccc;
      text-indent: 12px; }
    main form > div div {
      float: right;
      width: 183px;
      height: 52px;
      border: 1px solid #cccccc; }
    main form > div canvas {
      width: 183px !important;
      height: 52px !important; }
    main form > p {
      text-align: center; }
      main form > p:nth-of-type(6) input {
        background-color: #ff4c4c;
        font-size: 18px;
        color: white;
        cursor: pointer;
        border: none; }
      main form > p:last-of-type {
        text-align: left;
        margin-top: 10px; }
        main form > p:last-of-type a {
          padding-left: 32px;
          background: url(images/regiser_log.png) no-repeat left center;
          color: #2875cc;
          font-size: 18px; }
      main form > p span {
        display: block;
        height: 25px;
        line-height: 25px;
        color: red; }
    main form p input {
      width: 373px;
      height: 52px;
      border: 1px solid #cccccc;
      text-indent: 12px; }

